
const Color = ({color,setColor}) => {

    return (
        <form className='colorForm' onSubmit={(e) => e.preventDefault()}>
            <label htmlFor='search'>Color</label>
            <input
                id='color'
                type='text'
                role='text'
                placeholder='Color'
                value={color}
                onChange={(e) => setColor(e.target.value)}
            />
        </form>

    )}

export default Color